body {
  margin:0;
  padding: 0;
}
.bgimg {
  background-image: url('./wall.jpg');
  height: 100vh;
  background-size: cover;
  position: relative;
  font-family: 'Bigelow Rules', cursive;
  letter-spacing: 15px;
  font-size: 40px;
  font-weight: 900;
}
.bottomleft {
  position: absolute;
  top: 0;
  left: 16px;
}
.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
